<template>
  <div>
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-form-item prop="materialCodeOrName" class="fl">
          <el-input v-model.trim="query.materialCodeOrName" placeholder="物料编码或名称" class="filter-item" style="width: 178px;" @keyup.enter.native="searchInit" />
        </el-form-item>
        <el-form-item prop="materialGroupCode" class="fl">
          <el-input v-model.trim="query.materialGroupCode" placeholder="物料组编码或名称" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
      </el-form>
    </div>
    <el-row>
      <el-descriptions :column="3">
        <el-descriptions-item label="日期">{{ baseInfo.date || '---' }}</el-descriptions-item>
        <el-descriptions-item label="项目">{{ baseInfo.project || '---' }}</el-descriptions-item>
        <el-descriptions-item label="总账科目">{{ baseInfo.ledgerSubjects || '---' }}</el-descriptions-item>
        <el-descriptions-item label="工单号">{{ baseInfo.workOrderNumber || '---' }}</el-descriptions-item>
        <el-descriptions-item label="成本中心">{{ baseInfo.costCenter || '---' }}</el-descriptions-item>
      </el-descriptions>
    </el-row>
    <el-row>
      <table-com
        :ref="refName"
        :table-height-type="530"
        :selection="false"
        :selection-fixed="false"
        :loading="loading"
        :index="true"
        :table-column-sort="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="170"
        :show-operate="false"
        :refresh-key="refreshKey"
        @rowClick="handleRowClick"
      />
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
  </div>
</template>

<script>
import { getListApi, getHeadDetailApi } from '@/apis/modules/out-bound/reservation-form-detail'
import paginationPage from '@/mixins/paginationPage'
import { dictCodeMixins } from '@/mixins/dictionary'

export default {
  components: {
  },
  mixins: [paginationPage, dictCodeMixins],
  props: {
    id: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      query: {
        materialCodeOrName: '',
        materialGroupCode: ''
      },
      loading: false,
      baseInfo: {},
      params: {}, // 路由参数
      refName: 'baseTableRef',
      refreshKey: 0,
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'warehouseSapName',
          label: 'SAP仓库',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialCode',
          label: '物料编码',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: true,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '物料名称',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '评估类型',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        // {
        //   module: 'word',
        //   prop: 'inventoryNum',
        //   label: '库存数量',
        //   align: 'center',
        //   minWidth: '',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center'
        // },
        {
          module: 'word',
          prop: 'needNum',
          label: '需求数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'needDate',
          label: '需求日期',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialGroupCode',
          label: '物料组',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialGroupDesc',
          label: '物料组描述',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ]
    }
  },
  mounted() {
    this.params = this.$route.params
    this.init()
    this.getDetail()
      .then(() => {
        if (this.baseInfo.status === 3) {
          const ind = this.columnList.findIndex(c => c.prop === 'inventoryNum')
          this.columnList.splice(ind, 1)
        }
      })
  },
  methods: {
    init() {
      const params = {
        ...this.query,
        reservedNumber: this.params.id || '',
        page: {
          ...this.pageInfo
        }
      }
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 头部详情
    getDetail() {
      return new Promise((resolve, reject) => {
        getHeadDetailApi({
          billNumber: this.params.id || ''
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.baseInfo = data
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 重置
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    // 表格点击事件回调
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    }
  }
}
</script>

<style>

</style>
